.Button {
	padding: 14px 45px;
	height: 50px;
	background: #4d2392;
	border-radius: 8px;

	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 22px;
	letter-spacing: 0em;
	text-align: center;

	color: white;
	outline: none;
	cursor: pointer;

	&:disabled {
		opacity: 0.5;
	}
}
